@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color: #fff;
}


.main-wrapper p{
  font-family: 'Roboto', sans-serif;
  line-height: 1.5em;
  font-size: 14pt;
}


.navigation-bar {
  background-color: #dd6e42;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 25px 0 rgba(0, 0, 0, 0.1);
}
.navigation-bar a{
  margin-right: 10px;

}
.navigation-bar div{
  padding: 13px;
}

.nav-bar-option {
  font-size: 20px;
  padding: 10px;
  border-radius: 50px;
}
.nav-bar-option:hover {
  color: #e2eb98;
  -webkit-transition:color 2s ease-out, background 2s ease-in;
  /* ...and now override with proper CSS property */
  -webkit-transition:color .2s ease-out, background 2s ease-in;
  transition:color .2s ease-out, background 2s ease-in;
}


.hero{
  text-align: center;
  vertical-align: middle;
  height: 100vh;
}
.hero h1{
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
  margin-top: 200px;
  font-size: 72pt;
  text-shadow: 8px 8px #dd6e42;
}


.section{
  margin-top: 100px;
  margin-bottom: 100px;
}
.section h1{
  font-weight: 800 !important;
  color: #dd6e42 !important;
  font-size: 48pt;
  font-family: 'Open Sans', sans-serif;
}


.biohack-winners{
  padding: 30px;
  border-radius: 10px;
  background: #f7e1da;
  text-align: center;
  margin-bottom: 40px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 25px 0 rgba(0, 0, 0, 0.1);
}
.biohack-winners h4{
  color: #dd6e42;
}
.biohack-winners p{
  text-align: left;
  font-size: 12pt;
}
.icons{
  font-size: 20px;
  color: #dd6e42;
}
.biohack-winners img{
  width: 255px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
}

.cardBody{
  font-size: 14pt;
}


.contact{
  padding: 30px;
  background: #dd6e42;
  box-shadow: 4px 4px 4px 0px rgba(0, 0, 0, 0.1), 25px 6px 25px 0px rgba(0, 0, 0, 0.1);
}
.contact h5{
  color: #fff;
  font-size: 12pt;
}
.contact a{
  color: #fff;
  font-size: 30px;
}
.contact a:hover{
  color: #e2eb98;
  -webkit-transition:color 2s ease-out, background 2s ease-in;
  /* ...and now override with proper CSS property */
  -webkit-transition:color .2s ease-out, background 2s ease-in;
  transition:color .2s ease-out, background 2s ease-in;
}


@media only screen and (max-width: 800px){
  .hero h1{
    font-size: 48pt;
  }
  .section h1{
    font-size: 36pt;
  }
  .main-wrapper p{
    font-size: 12pt;
  }
}

.App {
  text-align: center;
}

