.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.virus{
  height : 500px;
  width: 650px;
}

.cheeringVirus{
  height : 400px;
  width: 400px;
}


.thinking-virus {
  height: 400px;
  width: 400px;
}


